.SearchCategories {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-bottom: 30px;
}

.SearchCategories__btn {

   display: block;
    outline: 0;
    border: 0;
    background-color: #ffffff;
    cursor: pointer;
    padding: 5px;
    text-transform: uppercase;
    white-space: nowrap;
    color: #424242;
    font-weight: 700;
    font-size: 16px;
    line-height: 1;
    border-radius: 2px;
    border: 3px solid #424242;
    -webkit-box-shadow: 2px 4px 2px 0 rgba(33,33,33,.5);
            box-shadow: 2px 4px 2px 0 rgba(33,33,33,.5);
    -webkit-transition: all .1s linear;
    -o-transition: all .1s linear;
    transition: all .1s linear;
}

.SearchCategories__btn:hover {
	background-color: #424242;;
	color: #fff;
}



