.StackList {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	list-style: none;
}

.StackList_item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	width: 13%;
	padding: 20px 10px;
	border: 3px solid #424242;
	border-radius: 4px;
}

.StackList_icon {
	display: block;
	width: 100%;
	background-position: 50%;
	background-size: cover;
}


